<template>
    <div id="write">
        <h2>发帖</h2>
        <form v-if="!submmited">
            <label>主题：</label>
            <input type="text" v-model="content.contentName">
            <label>内容：</label>
            <textarea cols="30" rows="10" v-model="content.content"></textarea>
            <button @click="writeContent">发帖</button>
        </form>
        <div v-if="submmited">博客发表成功</div>
    </div>
</template>
<script>
import axios from 'axios'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      content: {
        contentName: '',
        content: '',
        addTime: '',
        userName: ''
      },
      submmited: false
    }
  },
  computed: {
    ...mapState(['personal', 'allContent'])
  },
  methods: {
    writeContent () {
      if (this.$store.state.personal.userName) {
        let date = new Date()
        date = date.getFullYear() + '-' + date.getMonth() + '-' + date.getDate()
        this.content.addTime = date
        this.content.userName = this.$store.state.personal.userName
        const content = this.content
        this.$store.dispatch('addContent', content)
        axios.post('https://myblog-21019-default-rtdb.firebaseio.com/allContents.json', content)
          .then(data => {
          })
        this.submmited = true
      } else {
        alert('请登录账号')
      }
    }
  }
}
</script>
<style lang="less" scoped>
#write{
    max-width: 1200px;
    margin: 0 auto;
    form{
        label{
            display: block;
            font-size: 32px;
            margin: 10px 0;
        }
        input{
            display: block;
            width: 1000px;
            height: 30px;
            font-size: 30px;
        }
        textarea{
            width: 1000px;
            height: 300px;
            font-size: 26px;
        }
        button{
            display: block;
            margin: 20px 400px;
            width: 200px;
            height: 30px;
        }
    }
}
</style>
