// 接收组件通知触发mutation调用间接更新状态的方法的对象
import { ADD_CONTENT, RECEIVE_CONTENTS, RECEIVE_PERSONS, REMOVE_PERSON } from './mutation-types.js'
import axios from 'axios'
export default {
  addContent ({ commit }, content) {
    commit(ADD_CONTENT, { content })
  },
  reqContent ({ commit }) {
    axios.get('https://myblog-21019-default-rtdb.firebaseio.com/allContents.json')
      .then(data => data.data)
      .then(data => {
        var allContent = []
        for (const key in data) {
          data[key].id = key
          allContent.unshift(data[key])
        }
        commit(RECEIVE_CONTENTS, { allContent })
      })
  },
  reqPerson ({ commit }, userName) {
    axios.get('https://myblog-21019-default-rtdb.firebaseio.com/allPersons.json')
      .then(data => data.data)
      .then(data => {
        let personal = {}
        for (const key in data) {
          if (data[key].userName === userName) {
            personal = data[key]
            window.sessionStorage.setItem('personal', JSON.stringify(data[key]))
          }
        }
        commit(RECEIVE_PERSONS, { personal })
      })
  },
  removePerson ({ commit }) {
    window.sessionStorage.removeItem('personal')
    commit(REMOVE_PERSON)
  }
  // checkPerson ({ commit }, userName, password) {
  //   axios.get('https://myblog-21019-default-rtdb.firebaseio.com/allPersons.json')
  //     .then(data => data.data)
  //     .then(data => {
  //       let personal = {}
  //       for (const key in data) {
  //         if (data[key].us === password) {
  //           personal = data[key]
  //         }
  //       }
  //       commit(CHECK_PERSONS, { personal })
  //     })
  // }
}
