<template>
    <div id="register">
        <h1>注册账号</h1>
        <form>
            <label>用户名：</label>
            <input type="text" v-model="regForm.userName" @blur="checkName($event)">
            <label>密码：</label>
            <input type="password" v-model="regForm.password" @blur="checkPwd($event)">
            <label>确认密码：</label>
            <input type="password" @blur="isRight($event)">
            <button @click="post">注册</button>
        </form>
    </div>
</template>
<script>
import axios from '../../node_modules/axios'
export default {
  data () {
    return {
      regForm: {
        userName: '',
        password: ''
      },
      checkName (e) {
        if (e.target.value.length >= 15 || e.target.value.length <= 3) {
          alert('用户名小于三位或大于十五位')
          e.target.value = ''
        } else {
        }
      },
      checkPwd (e) {
        if (e.target.value.length >= 15 || e.target.value.length < 6) {
          alert('密码小于六位或大于十五位')
          e.target.value = ''
        } else {
        }
      },
      isRight (e) {
        if (e.target.value !== this.regForm.password) {
          alert('两次密码不一致')
        }
      }
    }
  },
  methods: {
    post () {
      if (this.$store.dispatch('reqPerson', this.regForm.userName)) {
        alert('该用户已存在,请重新注册')
      } else {
        let date = new Date()
        date = date.getFullYear() + '-' + date.getMonth() + '-' + date.getDate()
        this.regForm.startTime = date
        this.regForm.contents = []
        axios.post('https://myblog-21019-default-rtdb.firebaseio.com/allPersons.json', this.regForm)
          .then(data => {
            alert('注册成功')
          })
      }
    }
  }
}
</script>
<style lang="less" scoped>
#register{
    width: 350px;
    height: 400px;
    background-color: rosybrown;
    margin: 100px auto 0;
    border-radius: 10px;
    h1{
        text-align: center;
    }
    form{
        label{
            display: block;
            font-size: 24px;
            margin-top: 5px;
        }
        input{
            box-sizing: border-box;
            width: 100%;
            height: 30px;
            margin-top: 10px;
            margin-bottom: 10px;
        }
        button{
            width: 300px;
            height: 40px;
            margin-left: 25px;
        }
    }
}
</style>
