<template>
    <div class="right_nav">
        <div v-if="isOn" id="personalData">
            <div><a href="#">设置</a></div>
            <div class="person">
                <!-- <span><img :src="personal.userImg" alt=""></span> -->
                <p>{{personal.userName}}</p>
                <ul>
                    <li><router-link to="/personaldata">个人信息</router-link></li>
                    <li><router-link to="/personalspace">个人空间</router-link></li>
                    <li><a @click="removePerson">退出</a></li>
                </ul>
            </div>
        </div>
        <div v-if="!isOn" id="signIn">
            <router-link to="/signin">登录</router-link>
            <router-link to="/register">注册</router-link>
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  data () {
    return {
      isOn: false
    }
  },
  computed: {
    ...mapState(['personal'])
  },
  watch: {
    personal: {
      deep: true,
      handler: function (personal) {
        if (personal.userName) {
          this.isOn = true
        } else {
          this.isOn = false
        }
      }
    }
  },
  methods: {
    removePerson () {
      this.$store.dispatch('removePerson')
    }
  }
}
</script>
<style lang="less" scoped>
.right_nav {
    display: flex;
    width: 300px;
    div{
        text-align: center;
        line-height: 60px;
    };
    #personalData{
        width: 100%;
        display: flex;
        justify-content: space-around;
        :first-child{
            flex:1;
        }
        .person {
            flex:1;
            display: flex;
            align-items: center;
            position: relative;
            // span{
            //     flex:1;
            //     height: 40px;
            //     overflow: hidden;
            //     border-radius: 50%;
            //     img{
            //         width: 100%;
            //     }
            // };
            p{
                margin-left: 10px;
                // flex:4;
                text-align: left;
                color:#ccc;
            }
            ul{
                position: absolute;
                width: 80px;
                line-height: 25px;
                background-color: #ccc;
                top: 50px;
                left: 10px;
                border-radius: 5px;
                opacity: 0;
            }
            &:hover{
                & ul{
                    opacity: 1;
                }
            }
        }
    }
    #signIn{
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        a{
            width: 100px;
            height: 40px;
            line-height: 40px;
            border-radius: 10px;
            background-color: aqua;
            &:hover{
                background-color: brown;
                color: red;
            }
        }
    }
}
</style>
