// 包含多个由action触发的直接更新状态的方法的对象
import { ADD_CONTENT, RECEIVE_CONTENTS, RECEIVE_PERSONS, REMOVE_PERSON } from './mutation-types.js'
export default {
  [ADD_CONTENT] (state, { content }) {
    state.allContent.unshift(content)
  },
  [RECEIVE_CONTENTS] (state, { allContent }) {
    state.allContent = allContent
  },
  [RECEIVE_PERSONS] (state, { personal }) {
    state.personal = personal
  },
  [REMOVE_PERSON] (state) {
    state.personal = {}
  }
  // [CHECK_PERSONS] (state, { personal }) {
  //   state.personal = personal
  // }
}
