<template>
    <div id="singleblog">
        <table>
            <thead>
                <tr>
                    <th>{{content.contentName}}</th>
                    <th>{{content.addTime}}</th>
                </tr>
            </thead>
            <tbody>
                <article>{{content.content}}</article>
            </tbody>
        </table>
    </div>
</template>
<script>
// import { mapState } from 'vuex'
export default {
  data () {
    return {
      id: this.$route.params.id,
      content: {}
    }
  },
  created () {
    this.content = this.$store.state.allContent.filter(value => value.id === this.id)[0]
  }
}
</script>
<style lang="less" scoped>
#singleblog{
    max-width: 1200px;
    margin: 0 auto;
    table{
        width: 100%;
        border: 1px solid #ccc;
        tr{
            border: 1px solid #ccc;
            width: 100%;
            display: flex;
            justify-content: space-between;
            th{
                border-left: 1px solid #ccc;
            }
        }
    }
}
</style>
