<template>
    <div id="signIn">
        <h1>用户登录</h1>
        <form>
            <label>用户名：</label>
            <input type="text" v-model="signForm.userName">
            <label>密码：</label>
            <input type="password" v-model="signForm.password">
            <a href="#">注册账号</a>
            <a href="#">忘记密码?</a>
            <button @click="signIn()">登录</button>
        </form>
    </div>
</template>
<script>
export default {
  data () {
    return {
      signForm: {
        userName: '',
        password: ''
      }
    }
  },
  methods: {
    signIn () {
      this.$store.dispatch('reqPerson', this.signForm.userName)
      this.$router.push('/')
    }
  }
}
</script>
<style lang="less" scoped>
#signIn{
    width: 350px;
    height: 400px;
    background-color: rosybrown;
    margin: 100px auto 0;
    border-radius: 10px;
    h1{
        text-align: center;
    }
    form{
        label{
            display: block;
            font-size: 24px;
            margin-top: 10px;
        }
        input{
            box-sizing: border-box;
            width: 100%;
            height: 30px;
            margin-top: 10px;
        }
        a{
            display: inline-block;
            margin: 10px 0 10px 10px;
            &:hover{
                color:red;
            }
        }
        button{
            width: 300px;
            height: 40px;
            margin-left: 25px;
        }
    }
}
</style>
