<template>
    <div>
        <ul>
          <li>主题 <span class="addTime">发表时间</span></li>
          <li v-for="content in personal.contents" :key="content.contentName">
            <router-link :to="'/singleblog/' + content.id">{{content.contentName}}</router-link>
            <button @click="deleteContent(content.id)">删除</button>
            <span>{{content.addTime}}</span>
          </li>
        </ul>
    </div>
</template>
<script>
import { mapState } from 'vuex'
import axios from 'axios'
export default {
  computed: {
    ...mapState(['personal', 'allContent'])
  },
  created () {
    this.$store.state.personal.contents = []
    this.$store.state.allContent.forEach(content => {
      if (content.userName === this.$store.state.personal.userName) {
        this.$store.state.personal.contents.push(content)
      }
    })
  },
  beforeDestroy () {
    this.$store.state.personal.contents = []
  },
  methods: {
    deleteContent (id) {
      axios.delete('https://myblog-21019-default-rtdb.firebaseio.com/allContents/' + id + '.json')
        .then(response => {
          this.$store.dispatch('reqContent')
          this.$router.push('/')
        })
    }
  }
}
</script>
<style lang="less" scoped>
ul{
  border: 1px solid red;
  border-bottom: none;
  li{
    font-size: 22px;
    height: 40px;
    line-height: 40px;
    padding-left: 30px;
    border-bottom: 1px solid red;
    .addTime{
      padding-right: 80px;
    }
    button{
    float: right;
    margin-top: 10px;
    }
    span{
    float:right;
    padding-right: 20px;
    }
  }
}
</style>
